<template>
  <div class="row align-items-center mt-3">
    <form class="form-signin" @submit.prevent="login">
      <h1 class="h3 mb-3 font-weight-normal text-center"></h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input
        type="email"
        id="inputEmail"
        class="form-control"
        placeholder=""
        v-model="user.username"
        required
        autofocus
      />
      <label for="inputPassword" class="sr-only">Password</label>
      <input
        type="password"
        id="inputPassword"
        class="form-control"
        placeholder=""
        v-model="user.password"
        required
      />
      <button class="btn btn-lg btn-primary btn-block " type="submit">
        <span class="text-white" v-if="canLogin === false"></span>
        <font-awesome-icon
          v-else-if="canLogin === true"
          icon="spinner"
          class="fa-spin"
        />
      </button>
      <span class="text-danger">{{ error }}</span>
      <div>
        <span class="text-primary">:wheat0710@gmail.com</span><br />
        <span class="text-primary">:wheat0710</span>
        <p> Google Chrome  80  Cookie 
          <a href="https://hex.school/zVBbk"></a>
        </p>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: '',
        password: ''
      },
      error: '',
      canLogin: false
    };
  },
  methods: {
    async login() {
      this.canLogin = true;
      const api = `${process.env.VUE_APP_URL}/admin/signin`;
      const vm = this;
      const { data } = await this.$http.post(api, vm.user);
      if (data.success) {
        this.$bus.$emit('showSnackbar', true, '#81C784', 5000, '');
        this.$router.push('/admin/product');
      } else {
        this.$bus.$emit('showSnackbar', true, '#D32F2F', 5000, '');
        this.error = '';
      }
      this.canLogin = false;
    }
  },
  components: {}
};
</script>

