<template>
  <div>
    <div
      class="modal fade bd-example-modal-sm"
      id="couponCard"
      tabindex="-1"
      role="dialog"
      @keyup.enter="submitCoupon((id = aCoupon.id || ''))"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white" id="couponTop">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="!aCoupon.id"></span>
              <span v-else></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="couponName"></label>
              <input
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors.has('') }"
                id="couponName"
                name=""
                placeholder=""
                v-validate="'required'"
                v-model="aCoupon.title"
              />
              <span v-if="errors.has('')" class="text-danger">
                {{ errors.first('') }}</span
              >
            </div>
            <div class="form-group">
              <label for="fold"></label>
              <input
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors.has('') }"
                id="fold"
                name=""
                placeholder=""
                v-validate="'required'"
                v-model="aCoupon.percent"
              />
              <span v-if="errors.has('')" class="text-danger">
                {{ errors.first('') }}</span
              >
            </div>
            <div class="form-group">
              <label for="promoCode"></label>
              <input
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors.has('') }"
                id="promoCode"
                name=""
                placeholder=""
                v-validate="'required'"
                v-model="aCoupon.code"
              />
              <span v-if="errors.has('')" class="text-danger">
                {{ errors.first('') }}</span
              >
            </div>
          </div>
          <div class="modal-footer" style="position: relative">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="submitCoupon((id = aCoupon.id || ''))"
            >
              <span v-if="isSubmit === false"></span>
              <font-awesome-icon
                v-else-if="isSubmit === true"
                icon="spinner"
                class="fa-spin"
              />
            </button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    ></div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  props: {
    coupon: {
      type: Object,
      defalut: {}
    }
  },
  components: {},
  data() {
    return {
      aCoupon: {},
      isLoading: false,
      isSubmit: false
    };
  },
  methods: {
    toggleCard(canOpenCard) {
      canOpenCard
        ? $('#couponCard').modal('show')
        : $('#couponCard').modal('hide');
    },
    async submitCoupon(id) {
      this.isSubmit = true;
      await this.$validator.validate().then(async result => {
        if (result) {
          this.aCoupon.due_date = Date.now();
          const api = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/admin/coupon`;
          if (id) {
            await this.editCoupon(api, id);
          } else {
            await this.createCoupon(api);
          }
          new Promise(resolve =>
            this.$emit('getCoupons', this.$route.query.page, resolve)
          );
          this.toggleCard(false);
        } else {
          document.getElementById('couponTop').scrollIntoView();
        }
        this.$bus.$emit(
          'showSnackbar',
          true,
          '#81C784',
          5000,
          this.aCoupon.id ? '' : ''
        );
        this.isSubmit = false;
      });
    },
    async createCoupon(api) {
      const vm = this;
      await this.$http.post(api, {
        data: { ...vm.aCoupon, percent: vm.aCoupon.percent * 10 }
      });
    },
    async editCoupon(api, id) {
      const vm = this;
      await this.$http.put(api + `/${id}`, {
        data: { ...vm.aCoupon, percent: vm.aCoupon.percent * 10 }
      });
    }
  },
  watch: {
    coupon() {
      console.log(1);
      if (this.coupon.id) {
        this.aCoupon = { ...this.coupon, percent: this.coupon.percent / 10 };
      } else {
        this.aCoupon = { ...this.coupon };
      }

      this.errors.clear();
      this.toggleCard(true);
    }
  }
};
</script>
