<template>
  <div>
    <table class="table text-center p-0 mb-0">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="order in orders" :key="order.id">
          <td>{{ order.num }}</td>
          <td>{{ order.user ? order.user.name : '' }}</td>
          <td>
            <span v-if="order.is_paid" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>{{ order.total }}</td>
        </tr>
      </tbody>
    </table>
    <Pagination
      :pageUrl="'/admin/order'"
      :pagination="pagination"
      @getSomething="getOrders"
    ></Pagination>
  </div>
</template>

<script>
import Pagination from '@/components/common/Pagination';

export default {
  components: {
    Pagination
  },
  created() {
    this.getOrders();
  },
  data() {
    return {
      removeID: '',
      pagination: {},
      orders: []
    };
  },
  methods: {
    async getOrders(page = 1) {
      this.$bus.$emit('showLoading', true);
      const api = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/admin/orders?page=${page}`;
      const { data } = await this.axios.get(api);
      const { orders, pagination } = data;
      this.orders = [...orders];
      this.pagination = {
        ...pagination
      };
      console.log(this.orders);
      this.$bus.$emit('showLoading', false);
    }
  }
};
</script>
