<template>
  <div>
    <div
      class="modal fade"
      id="productCard"
      tabindex="-1"
      role="dialog"
      @keyup.enter="submitProduct((id = Aproduct.id || ''))"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="productTop">
              <span v-if="!Aproduct.id"></span>
              <span v-else></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{
                      'is-invalid': !Aproduct.image && errors.has('')
                    }"
                    id="image"
                    name=""
                    placeholder=""
                    v-validate="'required'"
                    v-model="Aproduct.image"
                  />
                  <span
                    v-if="!Aproduct.image && errors.has('')"
                    class="text-danger"
                  >
                    {{ errors.first('') }}</span
                  >
                </div>
                <div class="form-group">
                  <label for="customFile"
                    ><span>  </span>
                    <font-awesome-icon
                      v-if="canUpload"
                      icon="spinner"
                      class="fa-spin"
                    />
                  </label>
                  <input
                    type="file"
                    id="uploadImage"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('') }"
                    name=""
                    v-validate="'image|size:1000'"
                    ref="image"
                    @change="addImage"
                  />
                </div>
                <span v-if="errors.has('')" class="text-danger">
                  {{ errors.first('') }}</span
                >
                <img :src="Aproduct.image" class="img-fluid" alt="" />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('') }"
                    id="title"
                    name=""
                    placeholder=""
                    v-validate="'required'"
                    v-model="Aproduct.title"
                  />
                  <span v-if="errors.has('')" class="text-danger">
                    {{ errors.first('') }}</span
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      :class="{ 'is-invalid': errors.has('') }"
                      id="category"
                      name=""
                      placeholder=""
                      v-validate="'required'"
                      v-model="Aproduct.category"
                    />
                    <span v-if="errors.has('')" class="text-danger">{{
                      errors.first('')
                    }}</span>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      :class="{ 'is-invalid': errors.has('') }"
                      id="unit"
                      name=""
                      placeholder=""
                      v-model="Aproduct.unit"
                      v-validate="'required'"
                    />
                    <span v-if="errors.has('')" class="text-danger">{{
                      errors.first('')
                    }}</span>
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      :class="{ 'is-invalid': errors.has('') }"
                      id="origin_price"
                      name=""
                      placeholder=""
                      v-model="Aproduct.origin_price"
                      v-validate="'required|min_value:0'"
                    />
                    <span v-if="errors.has('')" class="text-danger">{{
                      errors.first('')
                    }}</span>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      oninput="value = value.replace(/[^\d]/g, '')"
                      :class="{
                        'is-invalid': Aproduct.origin_price
                          ? errors.has('')
                          : false
                      }"
                      id="price"
                      name=""
                      placeholder=""
                      v-model="Aproduct.price"
                      v-validate="
                        `required|min_value:0|max_value:${
                          Aproduct.origin_price ? Aproduct.origin_price : 0
                        }`
                      "
                      :disabled="!Aproduct.origin_price ? true : false"
                      :required="Aproduct.origin_price ? true : false"
                    />
                    <span
                      v-if="Aproduct.origin_price ? errors.has('') : false"
                      class="text-danger"
                      >{{ errors.first('') }}</span
                    >
                    <span v-if="!Aproduct.origin_price" class="text-primary">
                      
                    </span>
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('') }"
                    id="description"
                    name=""
                    placeholder=""
                    v-model="Aproduct.description"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('') }"
                    id="content"
                    name=""
                    placeholder=""
                    v-model="Aproduct.content"
                  ></textarea>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer" style="position: relative">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="submitProduct((id = Aproduct.id || ''))"
            >
              <span v-if="isSubmit === false"></span>
              <font-awesome-icon
                v-else-if="isSubmit === true"
                icon="spinner"
                class="fa-spin"
              />
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  props: {
    product: {
      type: Object,
      defalut: {}
    }
  },
  components: {},
  data() {
    return {
      canUpload: false,
      isLoading: false,
      isSubmit: false,
      Aproduct: {}
    };
  },
  methods: {
    toggleCard(canOpenCard) {
      canOpenCard
        ? $('#productCard').modal('show')
        : $('#productCard').modal('hide');
    },
    async submitProduct(id) {
      this.isSubmit = true;
      const obj = document.getElementById('uploadImage');
      obj.value = '';
      const vm = this;
      await this.$validator.validate().then(async result => {
        if (result) {
          const api = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/admin/product`;
          if (id) {
            await vm.editProduct(api, id);
          } else {
            await vm.createProduct(api);
          }
          new Promise(resolve =>
            vm.$emit('getProducts', this.$route.query.page, resolve)
          );
          this.toggleCard(false);
        } else {
          document.getElementById('productTop').scrollIntoView();
        }
      });
      this.$bus.$emit(
        'showSnackbar',
        true,
        '#81C784',
        5000,
        this.Aproduct.id ? '' : ''
      );
      this.isSubmit = false;
    },
    async createProduct(api) {
      await this.$http.post(api, { data: this.Aproduct });
    },
    async editProduct(api, id) {
      await this.$http.put(api + `/${id}`, {
        data: this.Aproduct
      });
    },
    async addImage() {
      this.canUpload = true;
      const image = this.$refs.image.files[0];
      const imageFormData = new FormData();
      imageFormData.append('image', image);
      const api = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/admin/upload`;
      const data = await this.axios.post(api, imageFormData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      this.Aproduct.image = data.data.imageUrl;
      this.canUpload = false;
    }
  },
  watch: {
    product() {
      this.Aproduct = { ...this.product };
      this.errors.clear();
      this.toggleCard(true);
    }
  }
};
</script>
