<template>
  <div>
    <div class="row justify-content-around">
      <div class="col-md-5">
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-if="orders.length === 0">
              <td></td>
            </tr>
            <tr v-else v-for="order in orders" :key="order.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeOrder(order.id)"
                >
                  <font-awesome-icon
                    v-if="!canRemoveProduct"
                    :icon="['fa', 'trash-alt']"
                  />
                  <font-awesome-icon v-else icon="spinner" class="fa-spin" />
                </button>
              </td>
              <td class="align-middle">
                {{ order.product.title }}
                <div class="text-success" v-if="order.coupon">
                  
                </div>
              </td>
              <td class="align-middle">
                {{ order.qty }}/{{ order.product.unit }}
              </td>
              <td class="align-middle text-right">{{ order.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right"></td>
              <td class="text-right">{{ total }}</td>
            </tr>
            <tr v-if="!(total === final_total)">
              <td colspan="3" class="text-right text-success"></td>
              <td class="text-right text-success">{{ final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm" v-if="orders.length !== 0">
          <input
            type="text"
            class="form-control"
            v-model="code"
            placeholder=""
          />
          <div class="input-group-append">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="useCoupon"
            >
              
            </button>
          </div>
        </div>
      </div>
      <form class="col-md-5" @submit.prevent="submitOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input
            type="email"
            class="form-control"
            name="Email"
            id="useremail"
            v-model="user.email"
            v-validate="'required|email'"
            placeholder=" Email"
            required
            :disabled="orders.length === 0"
          />
          <span v-if="errors.has('Email')" class="text-danger">
            {{ errors.first('Email') }}
          </span>
        </div>

        <div class="form-group">
          <label for="username"></label>
          <input
            type="text"
            class="form-control"
            name=""
            id="username"
            v-model="user.name"
            v-validate="'required'"
            placeholder=""
            required
            :disabled="orders.length === 0"
          />
          <span v-if="errors.has('')" class="text-danger">
            {{ errors.first('') }}
          </span>
        </div>

        <div class="form-group">
          <label for="usertel"></label>
          <input
            type="tel"
            class="form-control"
            name=""
            id="usertel"
            v-validate="'required|numeric'"
            v-model="user.tel"
            placeholder=""
            required
            :disabled="orders.length === 0"
          />
          <span v-if="errors.has('')" class="text-danger">
            {{ errors.first('') }}
          </span>
        </div>

        <div class="form-group">
          <label for="useraddress"></label>
          <input
            type="text"
            class="form-control"
            name=""
            id="useraddress"
            v-model="user.address"
            v-validate="'required'"
            placeholder=""
            required
            :disabled="orders.length === 0"
          />
          <span v-if="errors.has('')" class="text-danger">
            {{ errors.first('') }}
          </span>
        </div>

        <div class="form-group">
          <label for="comment"></label>
          <textarea
            id="comment"
            class="form-control"
            cols="30"
            rows="10"
            v-model="user.message"
            :disabled="orders.length === 0"
          ></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger">
            <span v-if="isSubmit === false"></span>
            <font-awesome-icon
              v-else-if="isSubmit === true"
              icon="spinner"
              class="fa-spin"
            />
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      code: '',
      total: 0,
      final_total: 0,
      canRemoveProduct: false,
      isSubmit: false,
      orders: [],
      user: {}
    };
  },
  created() {
    this.getOrders();
  },
  methods: {
    async getOrders() {
      this.$bus.$emit('showLoading', true);
      const data = await this.axios.get(
        `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/cart`
      );
      const { carts, total, final_total } = data.data.data;
      this.orders = [...carts];
      this.total = total;
      this.final_total = final_total;
      this.$bus.$emit('showLoading', false);
    },
    async useCoupon() {
      const vm = this;
      const data = await this.axios.post(
        `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/coupon`,
        { data: { code: vm.code } }
      );
      const { success } = data.data;
      console.log(success);
      if (success) {
        const { final_total } = data.data;
        this.final_total = final_total;
        this.$bus.$emit(
          'showSnackbar',
          true,
          '#81C784',
          5000,
          data.data.message
        );
      } else {
        this.$bus.$emit(
          'showSnackbar',
          true,
          '#D32F2F',
          5000,
          data.data.message
        );
      }
      this.code = '';
      this.getOrders();
    },
    async submitOrder() {
      this.isSubmit = true;
      const vm = this;
      await this.axios.post(
        `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/order`,
        { data: { user: vm.user } }
      );
      this.$bus.$emit('showSnackbar', true, '#81C784', 5000, '');
      this.getOrders();
      this.user = {};
      this.isSubmit = false;
      this.$router.push({ path: '/admin/order' });
    },
    async removeOrder(id) {
      this.canRemoveProduct = true;
      await this.axios.delete(
        `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_API_PATH}/cart/${id}`
      );
      this.$bus.$emit('showSnackbar', true, '#D32F2F', 5000, '');
      this.canRemoveProduct = false;
      this.getOrders();
    }
  },
  components: {}
};
</script>
